* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  max-width: 1200px;
  margin: 0 auto;
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #ffffff;
}
.body--scrolloff {
  overflow: hidden;
}
.header {
  width: 100%;
  position: fixed;
  z-index: 1;
}
.header__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 20px;
}
.header__title {
  font-size: 20px;
  font-weight: normal;
  text-transform: uppercase;
  color: #ffffff;
}
.nav {
  display: flex;
  justify-content: end;
  align-items: center;
}
.nav__list {
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 20px;
  list-style: none;
}
.nav__link {
  color: #ffffff;
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
  opacity: 0.7;
  transition: 0.3s all ease-in;
}
.nav__link:hover,
.nav__link:focus {
  text-decoration: underline;
  opacity: 1;
}
.main {
  width: 100%;
  height: 100vh;
  min-height: 600px;
}
.main__wrap {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.sliderfull {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  position: relative;
}
.slederfull-controls {
  height: min-content;
  position: absolute;
  z-index: 1;
  bottom: 80px;
  left: 50%;
  transform: translate(-50%, -50%);
  width: min-content;
  display: grid;
  grid-template-columns: repeat(2, min-content);
  align-items: center;
  grid-column-gap: 20px;
}
.slederfull-controls::before {
  content: '';
  position: absolute;
  width: 4px;
  height: 100%;
  right: 50%;
  transform: translateX(100%) rotate(15deg);
  top: 0;
  background-color: #ffffff;
}
.slederfull-controls__total {
  font-weight: bold;
  font-family: 'Lato', sans-serif;
  color: #ffffff;
}
.slederfull-controls__total--current {
  font-size: 23px;
  align-self: start;
}
.slederfull-controls__total--total {
  font-size: 18px;
  opacity: 0.7;
  align-self: end;
}
.sliderfull-items__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 50px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 1;
}
.sliderfull-items__button--next {
  right: 10px;
}
.sliderfull-items__button--prev {
  left: 10px;
}
.sliderfull-items__button-img {
  width: 100%;
  height: 100%;
}
.sliderfull-items__button-img:hover {
  opacity: 0.7;
}
.sliderfull-items__button-img:active {
  opacity: 0.3;
}
.sliderfull-indicators {
  z-index: 1;
  max-width: 350px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.sliderfull-indicators__item {
  list-style: none;
}
.sliderfull-indicators__button {
  margin: 0 3px;
  width: 40px;
  height: 10px;
  border: none;
  border-radius: 8px;
  background-color: #fff;
  opacity: 0.5;
  transition: 0.4s ease;
  cursor: pointer;
}
.sliderfull-indicators__button--active {
  background-color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.sliderfull-indicators__button:hover,
.sliderfull-indicators__button:focus {
  opacity: 0.8;
}
.sliderfull-indicators__button:active {
  transition: 0s;
  opacity: 1;
}
.sliderfull-items {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.sliderfull-items__inner {
  width: 100%;
  height: 100%;
  display: flex;
  transition: 0.6s all;
}
.sliderfull-items__item {
  text-align: center;
}
.sliderfull-items__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
